// "use client";

import React from "react";
import styles from "./style.module.scss";
import useProducts from "../hooks/useProducts";
import ProductContainer from "../components/ProductContainer";
import Carousel from "../components/Carousel";
import { Button } from "@mui/material";
import productService from "../services/product";
import ListResponse from "../types/listResponse";
import Product from "../types/product";
import LoadMoreButton from "./LoadMoreButton";

async function HomePage() {
    // const { products, getProducts, hasLoadAll, isLoading } = useProducts();
    const response: ListResponse<Product> | undefined = await productService.getProducts(null, "", {}, null);
    const products = response?.nodes ?? [];

    const loadMore = async () => {
        const response: ListResponse<Product> | undefined = await productService.getProducts(null, "", {}, null);
        const newData = response?.nodes ?? [];
        products.concat(newData);
    };

    return (
        <div className={styles.page}>
            <Carousel />
            <div>
                <ProductContainer className="mt-3 mb-3" products={products} />

                <LoadMoreButton onLoadMore={loadMore} />

                {/* {!hasLoadAll && (
                    <div className={styles.load_more}>
                        <Button
                            className={styles.load_more_button}
                            disabled={isLoading}
                            color="primary"
                            variant="outlined"
                            size="medium"
                            onClick={getProducts}
                        >
                            Tải thêm
                        </Button>
                    </div>
                )} */}
            </div>
        </div>
    );
}

export default HomePage;
